.editImageHolder{position:relative;min-height:40px;text-align:center}.editImageHolder .mediaControl{position:absolute;text-align:center;top:50%;left:50%;transform:translate(-50%, -50%);border-radius:5px;width:fit-content;z-index:100}.editImageHolder .mediaControl .btnControl{display:flex;align-items:center;justify-content:center;background-color:rgba(255,255,255,0.8);padding:0 10px;border-radius:3px;width:max-content}.editImageHolder .mediaControl .btnControl button{color:#4527a4;font-weight:bold}.editImageHolder .mediaControl .btnControl button.btnRed{color:red}.editImageHolder img{object-fit:cover}
